import { createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';

const _unstAddr =  /\.github\.io$/.test(window.location.hostname) ? '0x41798557fB98118f7430e2F443a073D5801005B1' : 
      '0x016E0512b7BE52FBf311B26a706457C22e2Cd9EA'; //--dev ---

const _defaultSetting = {ready: false, loading: {}, popup:false, screenModel:{}, _watcher:'',
      unst : _unstAddr,
      INFURA_API_KEY : '3af2ebd23289490dbe29d3034e2887ce',
      poNet : 'Rinkeby',
      poNetCode : 'rinkeby',
      po : [],
      currentWallet : {
        provider : null,
        account : null,
        balance : null,
        network : {}
      },
      contracts : {
        local : [],
        public : []
      }, networks : [],
      walletProvider: null,
      current: { contract : '', account : { account: '', network:''} }
  };

const reducerSetting = (state = _defaultSetting, action) => {
  state._watcher = '';
  state.contracts.local = !state.contracts.local ? [] : state.contracts.local;
  switch(action.type) {

    case 'addLoading':
      state._watcher = 'loading';
      state.loading[action.id] = new Date().getTime();
      return state;
    case 'removeLoading':
      state._watcher = 'loading';
      delete state.loading[action.id];
      return state;

    case 'popupTrigger':
      state._watcher = 'popupTrigger';
      state.popup = action.popup;
      return state;

    case 'alertTrigger':
      state._watcher = 'alertTrigger';
      state.alertMessage = action.alertMessage;
      return state;
// --- ethrume related --- //

  case 'valueCurrentWallet':
    state._watcher = 'valueCurrentWallet';
    state.valueCurrentWallet = action.value;
    return state;
    

// --- app related --- //
    case 'addedPo':
      state._watcher = 'addedPo';
      //const poidx = state.po.findIndex(v=>v.addresss === action.data.address);
      // state.po[poidx === -1 ? state.po.length : poidx] = action.data;
      return state;
  
    case 'loadPostOffices':
      state._watcher = 'loadPostOffices';
      state.po = action.data;
      return state;

    default:
      return state;
  }
};
const SettingStore = createStore(reducerSetting, composeWithDevTools());

export default SettingStore;
