import React, {useEffect, useState} from 'react';
import { Container } from 'react-bootstrap';

import { SettingStore } from '../stores';
import { useUNST } from '../contracts';
import { usePostOffice, useUIComponents, useUtils } from '../customHooks';
import Web3 from 'web3';
import  { SuezHub } from './';

export default (props)=> {
  const { copyableShow } = useUtils();

  const UNSTaddress = SettingStore.getState().unst;
  const { getUNSTInfo, getHubs } = useUNST('UNST', UNSTaddress);
  const [ unstInfo, setUnstInfo ] = useState({});
  
  const { getPostOffices } = usePostOffice();
  const {  fundingIcon } = useUIComponents()

  const initLoad = async ()=>{
    getHubs(v=>{
      if (v.status === 'success') {
        const data = [];
        for (const i in v.data) {
          data.push({name:null, address:v.data[i]});
        }
       // console.log('Loaded products contents', data);
        SettingStore.dispatch({ type: 'loadPostOffices', data : data});
      }
    });

    getUNSTInfo(v=>{
      if (v.status === 'success') {
        setUnstInfo(v.data);
      }
    })
  }
  
  useEffect(()=>{
    initLoad();
  
    const handleSubscribe = SettingStore.subscribe(() => {
      if (SettingStore.getState()._watcher === 'addedPo') {
        initLoad();
      }
    });
    return ()=> {
      handleSubscribe();
    }

  }, [])
  return <Container fluid={true} className="p-3 border border-warning rounded bg-tranparent-box"> 
  <Container fluid={true} className="p-0 pt-1 clearfix text-left text-warning" style={{minHeight:'5rem'}}>
    <span className="float-left m-0 mr-2 p-1">
      <h5 className="text-warning text">{unstInfo.symbol}
        {!unstInfo || !unstInfo.name? '' : <span className="ml-1 p-1" style={{fontSize:'1.1rem'}}>({unstInfo.name})</span>}
      </h5>
    </span>

    <span className="float-left m-0 mt-1 mr-2 p-1 bg-inverse border border-success rounded">
      Contract: {copyableShow(UNSTaddress,5)}
    </span>

    {/*<span className="float-left m-0 mt-2 mr-2 p-1 bg-inverse border border-success rounded">Pool: {unstInfo.poolSize}</span>*/}
    <span className="float-left m-0 mt-1 mr-2 p-1 bg-inverse border border-success  rounded">WEI Rate  : 
      {unstInfo.rate}</span>

    
    <span className="float-left m-0 mt-1 mr-2 p-1 bg-inverse border border-success rounded">
      Pool: {Web3.utils.fromWei(!unstInfo || !unstInfo.ethBalance? '0' : unstInfo.ethBalance ,'ether')} ETH</span>
    <Container className="clearfix"></Container>

    {fundingIcon()}

    {/*getPostOffices().map((v,k)=> itemIcon(v,k))*/}

    {getPostOffices().map((v,k)=> <SuezHub key={k} type="itemIcon" address={v.address}/>)}
    
    <Container className="clearfix"></Container>

  </Container>
  <Container className="clearfix"></Container>

</Container>
}
//   <span key={k} className="mr-3"> {copyableShow(v.address, 5)} -- {v.name} </span>;