import React, { useState, useEffect }  from 'react';
import { Container } from 'react-bootstrap';
import { SettingStore } from '../stores';

import { useUtils } from '.';
import { ImageLib } from '../comm';

export default ()=> {

  const [ postOffices, setPostOffices ] = useState([]);
  const { hashLink } = useUtils();

  useEffect(()=>{
    
    setPostOffices(SettingStore.getState().po)
    
    const handleSubscribe = SettingStore.subscribe(() => {
        if (SettingStore.getState()._watcher === 'loadPostOffices') {
          setPostOffices(SettingStore.getState().po)
        }
      });
      return ()=> {
        handleSubscribe();
      }
     
  }, []);

    const fundingBox = hashLink('/postOffice/funding',<Container fluid={true} className="m-0 p-3 alert-info rounded hover-shadow">
      <span className="mr-2 float-left">
        <ImageLib v="postOffice" setting={{className: 'border border-info shadow-sm p-1',  
          style: {width:'3rem'}}} />  
      </span>
    </Container>)

    const getPostOffices = ()=> postOffices;

  return { getPostOffices,  fundingBox  }
}