import React,  { useState, useEffect}  from 'react';
import { Container, Button, Form, Row, Col, Image } from 'react-bootstrap';
import useUNST from '../contracts/useUNST';
import { SettingStore } from '../stores';
import { Engine } from '../comm';

import { useUtils, usePostOffice,} from '../customHooks';
import { useParams, useHistory } from 'react-router';

import { ImageLib } from '../comm';

import  { SuezHub } from '.';

export default (props)=> {
  const eng = new Engine();
  const history =  useHistory();

  const { poAddress } = useParams();

  const {  createPostoffice, askPrice } = useUNST('UNST', 'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce',  SettingStore.getState().unst);
  const [ unstInfo, setUnstInfo ] = useState({});

  const [ formrec, setFormrec ] = useState({name:''});
  const [ formErr, setFormErr ] = useState( '' );

  const [ price, setPrice ] = useState(null);

  const [ submitted, setSubmitted ] = useState(false);
  
  const [ po, setPo ] = useState([]);

  const { getPostOffices } = usePostOffice();
/*, postOfficeCard */
  // const { postOfficeCard } = useSuezHub();

  const { hashLink } = useUtils();

  const handleTextChange = async (e, id)=> {
    const rec = formrec;
    rec[id] = e.target.value;
    setFormrec(rec);
    
  }
// setTm(new Date().getTime());

  useEffect(()=>{
   // setPo(SettingStore.getState().po);
    askPrice( v=>{ 
     if (v.status === 'success') { setPrice(v.data)}
    });
  }, []);

  const resetForm = ()=> {
    setSubmitted(false);
    setFormrec({name:''});
    setFormErr( '');
    SettingStore.dispatch({ type: 'addedPo'});
    history.push('/postOffices');
  }
  const btnSubmit = ()=> <Button className="btn btn-info m-1" onClick={()=>{
    if (!formrec.name || formrec.name.length < 4 ) {
      setFormErr( 'Name length should not be less than 4 charactors.');
      
      return true;
    } else {
      setFormErr( '');
    }
    
    eng.loadingOn();
    createPostoffice(formrec.name, v=>{
      if (v.status === 'success') {
        setSubmitted(true);
      }
      eng.loadingOff();
    });
  }}>Submit</Button>

  const btnCancel = ()=><Button className="btn btn-secondary m-1" onClick={resetForm}>Cancel</Button>
  
  const fundingForm  =  <Container fluid={true} className="p-3 bg-light rounded">

    <Container fluid={true} className="p-3 ">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded" 
              style={{height:'100%', width:'100%', backgroundColor:'#87CEEB'}}>
              <ImageLib v="postOffice" setting={{className:'', style:{width:'8rem'}}} />
              {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
            </Container>
        </Col>
        {!submitted ?  <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-secondary rounded p-3">
          <Form.Group className="m-0 p-0">
            <Form.Label className="p-0 m-0 text-info">
                <Container className="text-dark p-0 m-0 pb-2">
                  Create Post Office under Ethereum mainnet. Each post office contain 1,000,000 UNST and 1000 
                  placeholder projects. <br/>
                  <b className="text-info">Funding a post offic. Your minimum funding amount is 
                  <span className="p-0 pl-1 pr-1 text-danger">{price}</span> Ether</b>
                </Container>
              </Form.Label>
          </Form.Group>

          <Form.Control type="text" defaultValue={formrec.name}
            placeholder="Enter a office name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
            onChange={e=>handleTextChange(e, 'name')}/>

          {btnSubmit()} {btnCancel()}
          <span className="m-1 text-danger">{formErr}</span>
        </Col>
         :  <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-success rounded p-3">
            Your order has been Submitted! keeping refresh to update, 
            you will see your postoffice on the UNST.
            <Container className="text-center p-3">
             {hashLink('/postOffices/',<Button className="btn btn-info" onClick={resetForm}>OK</Button>)}
            </Container>
            
         </Col>}
    </Row>
    </Container>
  </Container>;

  const fundingBox = hashLink('/postOffice/funding',<Container fluid={true} className="m-0 p-3 alert-info rounded hover-shadow">
    <span className="mr-2 float-left">
      <ImageLib v="postOffice" setting={{className: 'border border-info shadow-sm p-1',  style: {width:'3rem'}}} />
      
    </span>

    <span className="float-left">
      <Container className="mr-3">Invest via Mainne</Container>
      <Container className="mr-3">Create Post Office under Ethereum mainnet. Each post office contain 1,000,000 UNST and 1000 
      placeholder projects. </Container>
    </span>
    <Container className="clearfix"/>
  </Container>)

  return (
    <Container fluid={true} className="p-3" style={{minHeight:'32rem'}}>

      {!poAddress ? '' :  poAddress === 'funding' ? fundingForm : <
          SuezHub address={poAddress}  type="mainCard" />}

      <Container fluid={true} className="p-0">
        <Container fluid={true} className="p-3 bg-light">
        Projects
          Projects 
        </Container>
      
        {/*(poAddress === 'funding') ? '' : getPostOffices().filter(v=>v.address !== poAddress ).
            map((v,k)=> <span key={k}>
              {hashLink('/postOffice/' + v.address, <SuezHub address={v.address}/>)}
  </span>)*/}
      </Container>
    </Container>
  );
}