import Web3 from 'web3';

export default (contractType, address)=> {
  const network =  'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce';
  
    const _provider = !Web3 || !Web3.givenProvider ? network : Web3.givenProvider;
    const isUpdateable = !Web3 || !Web3.givenProvider ? false : true;

    const abi = require(`./abis/suezHub`).default;
    const web3 = new Web3(_provider);

    const getContractInfo = async (callback) => {

        const accounts = await web3.eth.getAccounts();
        const contractobj = new web3.eth.Contract(abi,address);
        const name = await contractobj.methods.name().call();
        const symbol = await contractobj.methods.symbol().call();
       // const projects = await contractobj.methods._getAllTokensUrls().call();
      //  const symbol = await contractobj.methods._getContractData().call();
        const data = {
           name : name,
           symbol:symbol,
         //  projects  :  projects
        }
        if (callback) callback(  data );
    }

    return  { getContractInfo  }

}