import React, { useEffect }  from 'react';
import Web3 from 'web3';
import { SettingStore } from '../stores';
import { useEthereumNetwork } from '../contracts';

export default (contractType, contractAddr)=> {

    const network =  'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce';
    const { currentWallet } = useEthereumNetwork();

    const abi = require(`./abis/${contractType}`).default;
    const web3 = new Web3(network);
    const contractPublic = new web3.eth.Contract(abi,contractAddr);

    useEffect(()=>{
        const handleSubscribe = SettingStore.subscribe(() => {
            if (SettingStore.getState()._watcher === 'valueCurrentWallet') {
                console.log('6677=>', SettingStore.getState().currentWallet,  currentWallet);
            }
        });
        return ()=> {
            handleSubscribe();
        }
        
    }, [])

    const createPostoffice = async (_name, callback)=>{
        if (! currentWallet.web3) return true;
        const abi = require(`./abis/${contractType}`).default;
        const contract = new currentWallet.web3.eth.Contract(abi,contractAddr);  
        try {
            const q = await contract.methods.investHub(_name);
            const cfg = {
                from:currentWallet.account,
                to: contractAddr,
                value:web3.utils.toWei('1', 'finney')
            }
            const estGas = await q.estimateGas(cfg);
            const p = await q.send({...cfg, gas: Math.round(estGas * 1.05)});
            callback({status:'success'});
        } catch (e) {
            callback({status:'failure', message : e.message});
        }
    }

    const getHubs = async (callback) => {
        try {
            const info = await contractPublic.methods.getHubs().call();
            callback({status:'success', data:info});
        } catch (e) {
            callback({status:'failure', message : e.message});
        }
    }

    const getUNSTInfo = async (callback) => {
        try {
            const info = await contractPublic.methods.getUNSTinfo().call();
        } catch (e) {
            callback({status:'failure', message : e.message});
        }
    }

    const askPrice = async (callback) => {
        try {
            const q = await contractPublic.methods.askPrice().call();
            callback({status:'success', data:web3.utils.fromWei(q,'ether')});
        } catch (e) {
            callback({status:'failure', message : e.message});
        }
    }

    return  { createPostoffice, getUNSTInfo, getHubs, askPrice }

}