import React, {  useEffect, useState }  from 'react';
import { Container } from 'react-bootstrap';
import { Banner, Header, PostOffices, StillWorking, MyProjects , ProjectCreate  } from '../modules';
import { useUIComponents  } from '../customHooks';
import { Engine } from '../comm';

import { Switch, Route } from "react-router-dom";

export default (props)=> {
  const eng = new Engine();

  const { centrelBox, headerBox, cardListShow, bodyBox } = useUIComponents();
  const [ prodList , setProdList ] = useState([]);

  const loadProducts = ()=>{
    eng.get('/metaData/products.json', v=>{
      setProdList(v);
    });
  }
/*
  String.prototype.interpolate = function(params) {
    const names = Object.keys(params);
    const vals = Object.values(params);
    return new Function(...names, `return \`${this}\`;`)(...vals);
  }
*/
  useEffect(()=>{
    loadProducts();
  }, [])

  return (
    <Container fluid={true}  className="p-0 text-left bg-inverse" style={{minHeight:'58rem'}}>
      {centrelBox(<Banner/>)}
      {headerBox(<Header/>)}
      <Container fluid={true} className="p-0 pt-2 text-center text-secondary home-bg" style={{minHeight:'36rem'}}>
          <Switch>
            <Route exact path={[
                  '/'
                  ]}>
                  {bodyBox(cardListShow(prodList))}
            </Route>

            <Route exact path={[ '/postOffice/:poAddress', '/postOffices' ]}>
                {bodyBox(<PostOffices/>)}
            </Route>


            <Route exact path={[ '/projects/:poAddress/:projectAddr', '/projects/', '/myProjects/' ]}>
                {bodyBox(<MyProjects/>)}
            </Route>

            <Route exact path={[ '/project/:poAddress/', '/project/']}>
                {bodyBox(<ProjectCreate/>)}
            </Route>

            <Route path="/:title">
              {bodyBox(<StillWorking/>)}
          </Route>
          </Switch>
      </Container>

    </Container>
  );
}

